.main {
  background-image: url("http://unibuild.gtcreators.com/wp-content/uploads/revslider/first/slider-1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  &__slider {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 140px 20px 150px;
    height: 100vh;
    text-align: center;
    color: #fff; }
  &__title {
    font-size: 70px;
    font-weight: bold;
    margin: 0 0 50px;
    color: $mainColor;
    text-transform: uppercase;
    & span {
      color: #fff;
      display: block;
      width: 75%;
      margin: 0 auto; } }
  &__content {
    font-size: 16px;
    display: block;
    width: 50%;
    margin: 0 0 70px; }
  &__links {
    width: 27%;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center; }
  &__link {
    padding: 8px 25px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    background-color: $mainColor;
    border: 2px solid  $borderColor;
    color: $textColor;
    &:hover {
      background-color: $btnBg;
      color: $mainColor; }
    &-inactive {
      background-color: $btnBg;
      color: $mainColor;
      &:hover {
        background-color: $mainColor;
        color: $text2Color; } } } }

.about {
  position: relative;
  padding-top: 250px;
  background-image: url("http://unitech.gtcreators.com/wp-content/uploads/2017/06/bg-1.jpg?id=63");
  background-repeat: no-repeat;
  background-size: 100% 70%;
  background-position: 100% 100%;
  height: 85vh;
  &__directions {
    position: absolute;
    width: 80%;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    top: -100px;
    left: 10%; }
  &__info {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start; }
  &__aside {
    flex-basis: 65%;
    color: $text2Color;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start; }
  &__title {
    font-size: 50px;
    flex-basis: 20%;
    margin: 0; }
  &__subtitle {
    font-size: 16px;
    font-weight: bold;
    margin: 0;
    margin-bottom: 20px;
    color: $textColor; }
  &__content {
    font-size: 14px; }
  &__promo {
    width: 100%;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    &__item {
      width: 32%;
      padding: 10px 15px;
      text-transform: uppercase;
      font-size: 16px;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      & svg {
        height: 40px;
        width: 40px;
        margin-right: 20px;
        color: $mainColor; } } } }


.directions {
  &__item {
    padding: 20px 30px;
    border: 2px solid  $textColor;
    background-color: #f5f5f7;
    width: 32%; } }
